import React, { useState } from 'react';
import { Component } from 'react';
import { createPortal } from "react-dom";
// import '../../../assets/css/modal.css';


function Modal() {
//   const { message } = props;
  return createPortal(
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        position: "fixed",
        bottom: 30,
        left: 0,
        width: "100%",
        height: 50,
      }}
    >
      <div
        style={{
          width: "30%",
          textAlign: "center",
          borderRadius: 30,
          background: "grey",
          fontSize: 20,
          color: "white",
        }}
      >
        <p>테스트</p>
      </div>
    </div>,
    document.getElementById("modal")
  );
}

export default Modal;